import React from 'react';
// import { camera, menuOutline, close, power, powerOutline, powerSharp } from 'ionicons/icons';
import {
    IonButton,
    IonButtons,
    IonHeader,
    IonIcon,
    IonItem, IonItemDivider,
    IonLabel,
    IonList,
    IonPage,
    IonRange,
    IonText,
    IonTitle,
    IonToolbar,
    IonContent,
    IonInfiniteScroll, IonInfiniteScrollContent
} from '@ionic/react';
// import { htt } from '@ionic/react';
import {ellipsisHorizontal, ellipsisVertical, logoReact, personCircle, search, sunny} from 'ionicons/icons';
import $ from '../../bower_components/jquery/'
// import { RangeValue } from '@ionic/core';
let timeOut = new Date();

class Main extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            canvas: React.createRef(),
            ctx: null,
            brightness: 100,
            x: 70,
            y: 490,
            status: 1
        }
    }

    whiteWorm = () => {
        this.setState({
            status: 1
        })
        $.ajax({
            method: "GET",
            async: true,
            // crossDomain: true,
            url: `https://lamp.vyunak.co.ua/api/whiteWorm/${this.state.brightness}`
        }).done(() => {
            let hex = "#" + (this.rgbToHex(231 * (this.state.brightness * 0.01), 189 * (this.state.brightness * 0.01), 13 * (this.state.brightness * 0.01))).slice(-6);
            $('.color-select').css('background', hex);
            $('#err').html('');
        }).fail((jqXHR, error) => {
            $('#err').html(error + ', ' + jqXHR.readyState + ', ' + jqXHR.status + ', ' + jqXHR.state());
        })
    }

    trackMove = (pos1, pos2) => {
        // console.log(info)
        this.setState({
            x: pos1,
            y: pos2,
            status: 0
        })

        if (new Date() - timeOut > 20) {

            timeOut = new Date();

            let x1 = this.state.canvas.current.width / this.state.jCanvas.width();
            let y1 = this.state.canvas.current.height / this.state.jCanvas.height();
            let x = pos1 * x1;
            let y = pos2 * y1;

            // eslint-disable-next-line no-unused-expressions
            x > this.state.canvas.current.width ? x = this.state.canvas.current.width -1 : null;
            // eslint-disable-next-line no-unused-expressions
            y > this.state.canvas.current.height ? y = this.state.canvas.current.height - 1 : null;


            // console.log('==========')
            // console.log(x, y, this.state.canvas.current.width);
            let p = this.state.ctx.getImageData(x, y, 1, 1).data;
            let hex = "#" + ("000000" + this.rgbToHex(p[0] * (this.state.brightness * 0.01), p[1] * (this.state.brightness * 0.01), p[2] * (this.state.brightness * 0.01))).slice(-6);
            let link = `https://lamp.vyunak.co.ua/api/switchColor/${p[0]}/${p[1]}/${p[2]}/${this.state.brightness}/100`;

            $.ajax({
                method: "GET",
                async: true,
                // crossDomain: true,
                url: link
            }).done(() => {
                // console.log(asd);
                $('.color-select').css('background', hex);
                $('#err').html('');
            }).fail((jqXHR, error) => {
                $('#err').html(error + ', ' + jqXHR.readyState + ', ' + jqXHR.status + ', ' + jqXHR.state());
            })
        }
    }

    rgbToHex = (r, g, b) => {
        if (r > 255 || g > 255 || b > 255)
            // eslint-disable-next-line no-throw-literal
            throw "Invalid color component";
        return ((r << 16) | (g << 8) | b).toString(16);
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.state.brightness !== prevState.brightness) {
            if (this.state.status === 0)
                this.trackMove(this.state.x, this.state.y);
            else
                this.whiteWorm()
        }
    }

    updateGraphics = (nbSides, ctx, canvas, width, height) => {
        ctx.clearRect(0,0,canvas.width,canvas.height);
        // for(let i = 0; i < width; i++){
        //     let hue = (width / 360) * i / (2 * Math.PI);
        //     console.log(hue, width / Math.PI, i / (2 * Math.PI))
        //     ctx.strokeStyle = hue;
        //     ctx.beginPath();
        //     ctx.rect(i, 0, 1, height);
        //     ctx.fillStyle = this.HSLToHex(hue, 100, 50);
        //     ctx.fill();
        // }
        let gradient = ctx.createLinearGradient(0, height,width,height);

        let hue = [[255,0,0],[255,255,0],[0,255,0],[0,255,255],[0,0,255],[255,0,255],[255,0,0]];
        //цвета на шкале hue в rgb

        let color;
        for (let i=0; i <= 6;i++){

            color = 'rgb('+hue[i][0]+','+hue[i][1]+','+hue[i][2]+')';

            console.log(color)
            gradient.addColorStop(i/6, color);

        };

        ctx.fillStyle = gradient;
        ctx.fillRect(0,0, width ,height);
        // ctx.fill();
        // this.whiteWorm()
        // this.trackMove(this.state.x, this.state.y);
    }

    componentDidMount() {
        this.setState({
            ctx: this.state.canvas.current.getContext('2d'),
            jCanvas: $(this.state.canvas.current)
        })
        $(document).ready(() => {
            let canvas = $('canvas');
            let ctx = canvas[0].getContext('2d');
            let CX = canvas[0].width,
                CY = canvas[0].height;
            this.updateGraphics(250, ctx, canvas[0], CX, CY);

            // if (this.state.canvas.jCanvas != null)
            $(this.state.canvas.current).on('touchmove', (data) => {
                // console.log('====');
                let {top, left} = $(data.target).offset();
                let x1 = data.touches[0].clientX - left;
                let y1 = data.touches[0].clientY - top;
                this.trackMove(x1 < 0 ? 0 : x1, y1 < 0 ? 0 : y1);
                // console.log(data.touches[0].clientX, x)
            })

            $(this.state.canvas.current).on('mousemove', (data) => {
                // let {top, left} = $(data.target).offset();
                let x1 = data.offsetX - 2;
                let y1 = data.offsetY - 2;
                this.trackMove(x1 < 0 ? 0 : x1, y1 < 0 ? 0 : y1);
                // console.log(data.touches[0].clientX, x)
            })

        })
    }

    HSLToHex = (h,s,l) => {
        s /= 100;
        l /= 100;

        let c = (1 - Math.abs(2 * l - 1)) * s,
            x = c * (1 - Math.abs((h / 60) % 2 - 1)),
            m = l - c/2,
            r = 0,
            g = 0,
            b = 0;

        if (0 <= h && h < 60) {
            r = c; g = x; b = 0;
        } else if (60 <= h && h < 120) {
            r = x; g = c; b = 0;
        } else if (120 <= h && h < 180) {
            r = 0; g = c; b = x;
        } else if (180 <= h && h < 240) {
            r = 0; g = x; b = c;
        } else if (240 <= h && h < 300) {
            r = x; g = 0; b = c;
        } else if (300 <= h && h < 360) {
            r = c; g = 0; b = x;
        }
        // Having obtained RGB, convert channels to hex
        r = Math.round((r + m) * 255).toString(16);
        g = Math.round((g + m) * 255).toString(16);
        b = Math.round((b + m) * 255).toString(16);

        // Prepend 0s, if necessary
        if (r.length === 1)
            r = "0" + r;
        if (g.length === 1)
            g = "0" + g;
        if (b.length === 1)
            b = "0" + b;

        return "#" + r + g + b;
    }

    render() {
        return (
            <IonPage>
                <IonHeader>
                    <IonToolbar>
                        <IonButtons slot="start">
                            <IonButton disabled>
                                <IonIcon slot="icon-only" icon={logoReact} />
                            </IonButton>
                        </IonButtons>
                        <IonButtons slot="primary">
                            <IonButton onClick={this.whiteWorm} color="dark">
                                <IonIcon color="warning" slot="icon-only" md={sunny} />
                            </IonButton>
                        </IonButtons>
                        <IonTitle>Lamp app</IonTitle>
                    </IonToolbar>
                </IonHeader>
                <div className="container ">
                    <IonList>
                        <IonItem className="item-interactive">
                            <IonLabel>
                                <canvas ref={this.state.canvas} className="canvas-color" width="1000px" height="1000px"/>
                            </IonLabel>
                        </IonItem>

                        <IonItem className="item-interactive">
                            <IonLabel>
                                <div className="color-select" />
                                <div className="" id="err" />
                            </IonLabel>
                        </IonItem>
                        <IonItem>
                            <IonRange snaps={true} step={2} ticks={true} value={this.state.brightness} onIonChange={e => this.setState({brightness: e.detail.value})} min={0} max={100}>
                                <IonIcon size="small" slot="start" icon={sunny} />
                                <IonIcon slot="end" icon={sunny} />
                            </IonRange>
                        </IonItem>
                    </IonList>

                    {/*<strong>Tab 2</strong>*/}
                </div>
            </IonPage>
        )
    }
}

export default Main;